
.writeEmail {
  height: 53px;
  width: 100%;
  background: #fff;
  border-top: 1px solid #e2e2e2;
  display: flex;
  justify-content: center;
  align-items: center;
}
.writeEmail .mint-tab-item-label {
    padding-top: 8px;
    min-height: 30px;
    font-size: 14px;
}
.writeEmail .mint-tab-item {
    height: 43px;
    line-height: 43px;
    color: #fff;
    background: #4e8dff;
    margin: 0 15px;
    padding: 0;
    border-radius: 4px;
    font-size: 18px;
}
.writeEmail .foot-icon {
    height: 24px;
    width: 24px;
    font-size: 24px;
    margin-right: 2px;
}



